@import url(https://fonts.googleapis.com/css2?family=Rokkitt:wght@400;800&display=swap);
#editor-block{margin-bottom:1.5rem}#editor,#preview{background:#b1b4ba;padding:1rem 1.5rem;min-height:30vh}#editor{width:100%;height:30vh}.toolbar{background:#414042;color:white;display:flex;justify-content:space-between;align-items:center;border-radius:10px 10px 0 0}.toolbar-title,.button{margin:0.5rem 0.75rem 0.3rem 1.5rem}.toolbar-title{font-family:"Rokkitt",monospace,serif;font-weight:bold;letter-spacing:1.5px}.button:hover,.button:focus{cursor:pointer;color:#74ccf4}@media (max-width: 650px){#main-window{font-size:12px}#editor{height:50vh}img{width:100%}}@media (max-width: 750px) and (orientation: landscape){#editor{height:60vh}}*{margin:0}#main-window{background:#58595b;width:100%;min-height:100vh;font-family:monospace,Calibri,serif,Arial}#block-container{width:90%;max-width:1000px;margin:0 auto;padding:2rem 0}

